// 组件的配置信息

import { format } from '@/utils/date-utils'
const now = new Date()

export default {
    // 属性配置示例:
    // exmaple: {
    //     // 配置中显示的标题
    //     title: '',
    //     // 提供的示例数据
    //     sampleData: {},
    //     // 默认value 的初始值
    //     defaultValue: {},
    //     // 是否必须 (组件只能有1 个, 且不能删除, 不能隐藏)
    //     must: false,
    //     // 最大数量限制
    //     limit: 1,
    //     // 固定位置的排序(从1 开始)
    //     fixedSort: 0,
    //     // 验证方法, 返回非空表示验证失败
    //     // eslint-disable-next-line
    //     validate (item) { }
    // },
    templateHead: {
        title: '标题栏',
        type: 'templateHead',
        defaultValue: {
            title: '',
            icon: '',
            bgcolor: '#FFFFFF'
        },
        must: false,
        isShowEdit: true,
        limit: 1,
        validate(item) {
            if (!item.value.title) {
                return '请输入标题'
            }
            // if (!item.value.icon) {
            //     return '请选择图标'
            // }
        }
    },
    backMusic: {
        title: '背景音乐',
        type: 'backMusic',
        defaultValue: {
            music: '',
            bgcolor: '#004da1'
        },
        must: false,
        isShowEdit: true,
        limit: 1,
        validate(item) {
            if (!item.value.music) {
                return '请上传背景音乐'
            }
        }
    },
    carousel: {
        title: '轮播图',
        type: 'carousel',
        defaultValue: {
            height: '200px',
            images: []
        },
        must: false,
        isShowEdit: true,
        limit: 1,
        validate(item) {
            if (!item.value.images.length) {
                return '请选择轮播图图片'
            }
            if (
                item.value.url != null &&
                item.value.url != '' &&
                !item.value.url.match(/^https?:\/\/([^.]+\.)+[^.]+$/)
            ) {
                return '自定义的链接需要填写带http:// 或者https:// 的有效连接'
            }
        }
    },
    // productIntroduction: {
    //     title: '产品介绍',
    //     type: 'productIntroduction',
    //     sampleData: {},
    //     defaultValue: {},
    //     must: false,
    //     isShowEdit: false,
    //     limit: 1
    // },
    // relatedProduct: {
    //     title: '特别推荐',
    //     type: 'relatedProduct',
    //     sampleData: {},
    //     must: false,
    //     isShowEdit: false,
    //     limit: 1,
    //     defaultValue: {}
    // },
    antiCounterfeit: {
        title: '验证查询',
        tooltip: '防伪/防窜验证, 防伪码在生码明细的码详情中查看',
        type: 'antiCounterfeit',
        defaultValue: {
            configArea: 'query',
            buttonColor: '#185EF0',
            buttonText: '码上验证',
            buttonTextColor: '#ffffff',
            successTemplate: 1,
            failureTemplate: 2,
            //invalidTitle: '您所查询的产品已被多次查询!',
            invalidTitle: "暂无该产品相关信息",
            queryHistory: true,
            validTitle: '您所查询的产品是正品!',
            validateText: ''
        },
        sampleData: {},
        must: false,
        limit: 1
    },
    text: {
        title: '文本',
        type: 'text',
        defaultValue: {
            text: '',
            style: ''
        },
        must: false,
        isShowEdit: true,
        limit: 10,
        validate(item) {
            if (!item.value.text) {
                return '请输入文本'
            }
        }
    },
    video: {
        title: '视频',
        type: 'video',
        defaultValue: {
            src: ''
        },
        must: false,
        isShowEdit: true,
        limit: 4,
        validate(item) {
            if (!item.value.src) {
                return '请上传视频'
            }
        }
    },
    pictures: {
        title: '图片',
        type: 'pictures',
        defaultValue: {
            images: [],
            link: ''
        },
        must: false,
        isShowEdit: true,
        limit: 10,
        validate(item) {
            if (item.value.images.length == 0) {
                return '请至少上传1 张图片'
            }
            const link = item.value.link
            if (link && !link.match(/^https?:\/\/([^.]+\.)+[^.]+$/)) {
                return '自定义的链接需要填写带http:// 或者https:// 的有效连接'
            }
        }
    },
    productInfo: {
        title: '对象信息',
        type: 'productInfo',
        sampleData: {},
        must: false,
        isShowEdit: true,
        limit: 1,
        defaultValue: {}
    },
    stylebox: {
        title: '宫格',
        type: 'stylebox',
        must: false,
        isShowEdit: true,
        limit: 4,
        defaultValue: {
            rows: [
                [
                    {
                        size: 1,
                        title: '标题',
                        linkType: 'none',
                        href: '',
                        fonticon: '',
                        imgIcon:
                            'https://ztl-admin-gongyema-com.obs.cn-east-2.myhuaweicloud.com/image-bz/202206/1534402386989289472.png',
                        forecolor: '#000000',
                        titleAlign: 'center',
                        backcolor: '#FFFFFF'
                    },
                    {
                        size: 1,
                        title: '标题',
                        linkType: 'none',
                        href: '',
                        fonticon: '',
                        imgIcon:
                            'https://ztl-admin-gongyema-com.obs.cn-east-2.myhuaweicloud.com/image-bz/202206/1534402386989289472.png',
                        forecolor: '#000000',
                        titleAlign: 'center',
                        backcolor: '#FFFFFF'
                    },
                    {
                        size: 1,
                        title: '标题',
                        linkType: 'none',
                        href: '',
                        fonticon: '',
                        imgIcon:
                            'https://ztl-admin-gongyema-com.obs.cn-east-2.myhuaweicloud.com/image-bz/202206/1534402386989289472.png',
                        forecolor: '#000000',
                        titleAlign: 'center',
                        backcolor: '#FFFFFF'
                    }
                ]
            ]
        },
        validate(item) {
            const row = item.value.rows[0]
            for (const cell of row) {
                if (cell.linkType === 'normal') {
                    if (!cell.href || !cell.href.match(/^https?:\/\/([^.]+\.)+[^.]+$/)) {
                        return '自定义的链接需要填写带http:// 或者https:// 的有效连接'
                    }
                } else if (cell.linkType === 'qrCodeScan') {
                    if (!cell.qrCodeImage) {
                        return '关注微信链接需要上传二维码'
                    }
                } else if (cell.linkType === 'kefuMobile') {
                    if (!cell.kefuMobile) {
                        return '请填写电话号码'
                    }
                } else if (cell.linkType === 'weixinWeapp') {
                    if (
                        cell.weixinWeappPath === undefined ||
                        cell.weixinWeappPath === '' ||
                        cell.weixinWeappUserName === undefined ||
                        cell.weixinWeappUserName === ''
                    ) {
                        return '如果选择微信小程序，小程序原始id 和 链接网址 为必填项!'
                    }
                }
            }
        }
    },
    // productBarcode: {
    //     title: '产品条码',
    //     type: 'productBarcode',
    //     defaultValue: {},
    //     sampleData: {
    //         code: {
    //             barCode: '8916318143811113'
    //         }
    //     },
    //     limit: 1,
    //     must: false,
    //     isShowEdit: false
    // },
    productQualityGuaranteePeriod: {
        title: '保质期',
        type: 'productQualityGuaranteePeriod',
        defaultValue: {},
        sampleData: {
            batch: {
                productionDate: '2020-01-01',
                beyondShelfLifeDate: format(now, 'yyyy-MM-dd')
            }
        },
        limit: 1,
        must: false,
        isShowEdit: false
    },
    // productPrice: {
    //     title: '产品价格',
    //     type: 'productPrice',
    //     defaultValue: {},
    //     sampleData: {},
    //     limit: 1,
    //     must: false,
    //     isShowEdit: false
    // },
    productImages: {
        title: '对象图片',
        type: 'productImages',
        defaultValue: {},
        sampleData: {},
        limit: 1,
        must: false,
        isShowEdit: false
    },
    codeInformation: {
        title: '标识码',
        type: 'codeInformation',
        defaultValue: {},
        sampleData: {
            code: {
                idisCode: '88.118.8/KIT001'
            }
        },
        limit: 1,
        must: false,
        isShowEdit: false
    }
    // ,
    // templateIframe: {
    //     title: 'iframe',
    //     type: 'templateIframe',
    //     defaultValue: {
    //         href: '',
    //         height: 200
    //     },
    //     must: false,
    //     isShowEdit: true,
    //     limit: 4,
    //     validate(item) {
    //         if (!item.value.href) {
    //             return '请输入iframe 地址!'
    //         }
    //     }
    // }
}
