<template>
    <div class="home-msg">
        <div class="home-msg-phone">
            <div class="home-msg-cont">
                <div
                    class="child-elements"
                    style="position:relative"
                    v-for="(item, k) in template.items"
                    :key="item.id"
                    :draggable="true"
                    @dragstart.self="dragstartEvent(item, k)"
                    @dragend.self="dragendEvent"
                    @dragenter.self="dragenterEvent"
                    @dragleave.self="dragleaveEvent"
                    @dragover.self="dragoverEvent"
                    @click.stop="selectClick(item)"
                    v-show="item.visible && item.type !== 'backMusic'"
                    :id="`homeMsg-${item.id}`"
                    :style="{ border: item.id === shineId ? '1px dashed #6074B6' : '' }"
                >
                    <div class="click-btn" v-if="item.type !== 'backMusic'" v-show="item.id === shineId ? true : false">
                        <div
                            style="border-right: 1px solid white;"
                            class="edit-btn"
                            v-if="CONFIG[item.type].isShowEdit"
                            @click.stop="selectItem2(item, k)"
                        >
                            编辑
                        </div>
                        <div
                            v-if="!CONFIG[item.type].must"
                            class="edit-btn"
                            style="cursor: pointer;color:'white'"
                            @click.stop="remove(item)"
                        >
                            删除
                        </div>
                    </div>
                    <template-head
                        v-if="item.type === 'templateHead'"
                        :item="{
                            value: {
                                icon: item.value.icon,
                                title: item.value.title || '请在配置中输入标题',
                                bgcolor: item.value.bgcolor
                            }
                        }"
                    ></template-head>
                    <template-carousel v-if="item.type === 'carousel'" :item="item"></template-carousel>
                    <product-desc v-if="item.type === 'productIntroduction'" :product="product"></product-desc>
                    <related-product v-if="item.type === 'relatedProduct'" :list="relatedProduct"></related-product>
                    <validate-search-edit
                        v-if="item.type === 'antiCounterfeit'"
                        :productName="product.productName"
                        :tenant="{ name: '测试企业' }"
                        :item="item"
                    ></validate-search-edit>
                    <template-text v-if="item.type === 'text'" :item="item"></template-text>
                    <template-video v-if="item.type === 'video'" :item="item"></template-video>
                    <template-pictures v-if="item.type === 'pictures'" :item="item"></template-pictures>
                    <product-info
                        v-if="item.type === 'productInfo'"
                        :item="item"
                        :sourceFlag="sourceFlag"
                        :code="code"
                        :codeAttrs="productAttrs"
                        :batch="CONFIG.productInfo.sampleData.batch"
                    ></product-info>
                    <template-stylebox v-if="item.type === 'stylebox'" :item="item"></template-stylebox>
                    <code-information
                        v-if="item.type === 'codeInformation'"
                        :item="item"
                        :code="CONFIG.codeInformation.sampleData.code"
                    ></code-information>
                    <product-barcode
                        v-if="item.type === 'productBarcode'"
                        :item="item"
                        :code="{ barCode: product.barCode }"
                    ></product-barcode>
                    <product-images
                        v-if="item.type === 'productImages'"
                        :item="item"
                        :images="productImages"
                    ></product-images>
                    <product-price v-if="item.type === 'productPrice'" :item="item" :code="{ price }"></product-price>
                    <product-quality-guarantee-period
                        v-if="item.type === 'productQualityGuaranteePeriod'"
                        :item="item"
                        :code="CONFIG.productQualityGuaranteePeriod.sampleData.code"
                        :batch="CONFIG.productQualityGuaranteePeriod.sampleData.batch"
                    ></product-quality-guarantee-period>
                    <!-- <template-iframe v-if="item.type === 'templateIframe'" :item="item"></template-iframe> -->
                </div>
                <div v-show="template.config.showBottom" style="height: 60px"></div>
            </div>
        </div>
        <div class="home-msg-float" @click.stop="musicClick(musicComp)">
            <base-music
                v-if="musicComp"
                v-show="musicComp.visible"
                :item="{
                    value: {
                        music: musicComp.value.music,
                        bgcolor: musicComp.value.bgcolor
                    }
                }"
                style="float: right; height: 40px; width: 40px; margin-bottom: 10px; margin-right: 10px;"
            ></base-music>
            <bottom-navigator
                v-show="template.config.showBottom"
                style="border-radius: 0 0 15px 15px;"
                active="home"
                :code="{}"
            ></bottom-navigator>
        </div>
    </div>
</template>

<script>
const defaultIcon = require('@/assets/images/logo.png')
import RelatedProduct from '@/components/product-template/RelatedProduct'
import ProductDesc from '@/components/product-template/ProductDesc'
import ProductInfo from '@/components/product-template/ProductInfo'
import ValidateSearchEdit from '@/components/configuration/ValidateSearchEdit'
import TemplatePictures from '@/components/product-template/TemplatePictures'
import TemplateVideo from '@/components/product-template/TemplateVideo'
import TemplateStylebox from '@/components/product-template/TemplateStylebox'
import TemplateText from '@/components/product-template/TemplateText'
import TemplateHead from '@/components/product-template/TemplateHead'
import TemplateCarousel from '@/components/product-template/TemplateCarousel'
import CodeInformation from '@/components/product-template/CodeInformation'
import ProductImages from '@/components/product-template/ProductImages'
import ProductBarcode from '@/components/product-template/ProductBarcode'
import ProductPrice from '@/components/product-template/ProductPrice'
import ProductQualityGuaranteePeriod from '@/components/product-template/ProductQualityGuaranteePeriod'
import CONFIG from '../config'
import { getJsonResult } from '@/utils/server-wrapper'
import BaseMusic from '@/components/product-template/BaseMusic'
import BottomNavigator from '@/components/product-template/BottomNavigator'
// import TemplateIframe from '@/components/product-template/TemplateIframe'

export default {
    props: {
        template: Object,
        sourceFlag: Number | String
    },
    data() {
        const defaultProduct = {
            productName: '(请选择对象)',
            remark: `(请选择对象)`,
            barCode: '1234567890'
        }
        const defaultCode = {
            productName: '(请选择对象)',
            productNo: '(请选择对象)',
            barCode: '(请选择对象)',
            categoryName: '(请选择对象)'
        }
        return {
            defaultIcon,
            shineId: 0,
            CONFIG,
            defaultProduct,
            product: defaultProduct,
            productImages: [],
            productAttrs: [],
            defaultCode,
            code: defaultCode,
            price: '',
            relatedProduct: []
        }
    },
    computed: {
        musicComp() {
            const list = this.template.items.filter(i => i.type === 'backMusic')
            return list.length == 0 ? null : list[0]
        }
    },
    watch: {
        'template.productId': {
            handler: async function() {
                const productId = this.template.productId
                if (!productId) {
                    this.product = this.defaultProduct
                    this.code = this.defaultCode
                    this.productImages = []
                    this.productAttrs = []
                    return
                }
                const product = await getJsonResult(`/businessObject/detail?id=${productId}`)
                this.product = product
                this.$emit('changeSourceFlag', product.sourceFlag)
                this.productImages = product.businessObjectImgBOList.map(i => i.imgPath)
                this.productAttrs = product.businessObjectAttrBOList
                this.code = {
                    productName: product.productName,
                    //productNo: product.productNo,
                    //barCode: product.barCode,
                    categoryName: product.categoryName
                }
                //this.price = product.price
            },
            immediate: true
        },
        'template.id': {
            handler: async function() {
                if (!this.template.id) {
                    this.relatedProduct = []
                    return
                }
                // TODO 和后端沟通 暂时隐藏
                // let relatedProduct = await getJsonResult(`/wx/item/relatedProduct?templateId=${this.template.id}`)
                // for (let item of relatedProduct) {
                //     item.productImage = item.productImage || defaultIcon
                // }
                // this.relatedProduct = relatedProduct
            }
        }
    },
    methods: {
        //点击模块
        selectClick(item) {
            this.shineId = item.id
        },
        // 选择项目的回调
        selectItem2(item, k) {
            this.shineId = item.id
            this.$emit('selectItem2', { item, k })
        },
        //音乐点击
        musicClick: function(item) {
            this.$emit('selectItem2', { item })
        },
        remove(item) {
            this.$confirm('确定要删除该组件吗？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                closeOnClickModal: false,
                type: 'warning'
            })
                .then(async () => {
                    this.$emit('removeItem', item)
                })
                .catch(() => {})
        },
        dragstartEvent(item, k) {
            const self = this
            self.shineId = item.id
            self.$emit('selectItem2', { item, k })
            self.$store.commit('setDragElement', event.currentTarget)
        },
        dragendEvent(ev) {
            ev.preventDefault()
        },
        dragenterEvent(ev) {
            const self = this
            if (self.$store.state.dragElement != ev.target) {
                ev.target.parentNode.insertBefore(self.$store.state.dragElement, ev.target)
                var list = ev.target.parentNode.children
                var listID = []
                for (var q = 0; q < list.length; q++) {
                    var a = list[q].id.slice(8)
                    listID.push(a)
                }
                var items = self.template.items
                var newItems = []
                for (var i = 0; i < listID.length; i++) {
                    for (var j = 0; j < items.length; j++) {
                        if (items[j].id == listID[i]) {
                            newItems.push(items[j])
                        }
                    }
                }
                self.template.items = newItems
            }
        },
        dragleaveEvent(ev) {
            const self = this
            if (self.$store.state.dragElement != ev.target) {
                if (
                    self.lock &&
                    (ev.target == ev.target.parentNode.lastElementChild || ev.target == ev.target.parentNode.lastChild)
                ) {
                    ev.target.parentNode.appendChild(self.$store.state.dragElement)
                    self.lock = false
                    var list = ev.target.parentNode.children
                    var listID = []
                    for (var q = 0; q < list.length; q++) {
                        var a = list[q].id.slice(8)
                        listID.push(a)
                    }
                    var items = self.template.items
                    var newItems = []
                    for (var i = 0; i < listID.length; i++) {
                        for (var j = 0; j < items.length; j++) {
                            if (items[j].id == listID[i]) {
                                newItems.push(items[j])
                            }
                        }
                    }
                    self.template.items = newItems
                } else {
                    self.lock = true
                }
            }
        },
        dragoverEvent(ev) {
            ev.preventDefault()
        },
        selectItem(id) {
            this.shineId = id
            document.getElementById('homeMsg-' + id).scrollIntoView()
        }
    },
    mounted() {},
    components: {
        RelatedProduct,
        ProductDesc,
        ProductInfo,
        ValidateSearchEdit,
        TemplatePictures,
        TemplateVideo,
        TemplateStylebox,
        TemplateHead,
        TemplateText,
        TemplateCarousel,
        CodeInformation,
        ProductImages,
        ProductPrice,
        ProductQualityGuaranteePeriod,
        ProductBarcode,
        BaseMusic,
        BottomNavigator
        // ,TemplateIframe
    }
}
</script>

<style lang="scss" scoped>
.child-elements {
    margin-top: 5px;
    margin-bottom: 5px;
}
.child-elements div {
    pointer-events: none;
}
.child-elements .click-btn .edit-btn {
    pointer-events: initial;
    z-index: 1000;
}
.click-btn {
    position: absolute;
    bottom: 0px;
    right: 2px;
    display: flex;
    justify-content: space-around;
    padding: 5px 0;
    background: black;
    color: white;
    font-size: 13px;
    border-radius: 5px;
    z-index: 1000;
}
.edit-btn {
    cursor: pointer;
    padding: 0 10px;
}
.child-elements:hover {
    border: 1px dashed #6074b6;
}
.child-elements:hover .edit-btn {
    display: flex;
}
.home-msg {
    position: relative;
    flex-shrink: 0;
}

.home-msg-phone {
    background: url('~@/assets/images/mobile.png') no-repeat center;
    background-size: contain;
    height: 750px;
    width: 360px;
    padding: 40px 10px 20px 13px;
}
.home-msg-cont {
    border-radius: 0 0 20px 20px;
    background-color: #f7f8fa;
    overflow-y: auto;
    height: 100%;
}
.home-msg-float {
    position: absolute;
    bottom: 20px;
    width: 336px;
    margin-left: 13px;
    z-index: 10;
}
.mh-icon {
    font-size: 20px;
    color: #fff;
    margin-right: 20px;
}
.mc-wrap {
    background-color: #fff;
    padding: 15px;
    &-title {
        color: #1c2023;
        font-size: 16px;
        text-align: left;
    }
}
.mc-link-circle {
    background-color: #e0e0e0;
    width: 20px;
    height: 20px;
    border-radius: 20px;
}

.item-selected {
    border: 1px orange solid;
}
</style>
