<template>
    <div class="f14">
        <div v-show="stage === 'query'" class="validate-edit">
            <!-- <div class="fx-dirv-h" style="margin-top: 10px">
                <img style="margin: 0px; height: 48px" :src="require('@/assets/images/zheng.png')" />
            </div> -->
            <div class="public-title">
                <div>验证查询</div>
                <img src="@/assets/images/genuine.png" alt="" />
            </div>
            <el-form ref="form" class="flex-all-center" style="margin-top: 16px">
                <el-form-item label-width="0">
                    <el-input class="vc-input flex-1" placeholder="请输入防伪码查询" @click="() => {}">
                        <template slot="append">
                            <el-button
                                class="vs-btn c-white"
                                :style="{
                                    'background-color': item.value.buttonColor,
                                    color: item.value.buttonTextColor
                                }"
                                >{{ item.value.buttonText }}</el-button
                            >
                        </template>
                    </el-input>
                </el-form-item>
            </el-form>
        </div>
        <div v-show="stage == 'success'" class="query-result">
            <p style="font-weight: bold; color: #38bf4d; margin-top: 20px">
                {{ item.value.validTitle }}
            </p>
            <p style="font-weight: bold; font-size: 18px; margin: 20px 0 20px 0">{{ productName }}</p>
            <p class="result-text">{{ successText }}</p>
            <div style="width: 100%; text-align: right; position: relative; top: -80px; right: 15px; height: 20px">
                <img :src="require('@/assets/images/validate-stamp-success.png')" style="width: 100px" />
            </div>
        </div>
        <div v-show="stage === 'failure'" class="query-result">
            <div>
                <p style="font-weight: bold; color: #fb0000; margin-top: 20px">
                    {{ item.value.invalidTitle }}
                </p>
                <p style="font-weight: bold; font-size: 18px; margin: 20px 0 20px 0">{{ productName }}</p>
                <p class="result-text">{{ failureText }}</p>
            </div>
            <router-link
                v-if="item.value.queryHistory"
                :to="`/check-code-history?idisCode=${encodeURIComponent(idisCode)}`"
                style="margin-top: 20px; text-decoration: none; color: gray"
            >
                点击查看最近10条记录
            </router-link>
        </div>
        <audio ref="audio"></audio>
    </div>
</template>

<script>
import { getJsonResult } from '@/utils/server-wrapper'

export default {
    props: {
        item: Object,
        idisCode: String,
        productName: String,
        tenant: Object
    },
    data() {
        return {
            stage: 'query',
            successText: '',
            failureText: '',
            voiceText: ''
        }
    },
    watch: {
        'item.value.configArea': function(configArea) {
            if (configArea) {
                this.stage = configArea
            }
        },
        'item.value.successTemplate': {
            handler: async function(id) {
                // 和后端沟通后 因2023.06版本将防伪模板删除 故此处文案先写死
                // let url = `/wx/securityTemplate?id=${id || ''}&scenario=success`
                // const template = await getJsonResult(url)
                // this.successText = this.replaceText(template.text)
                this.successText = this.replaceText('您所查询的产品是正品!')
            },
            immediate: true
        },
        'item.value.failureTemplate': {
            handler: async function(id) {
                // let url = `/wx/securityTemplate?id=${id || ''}&scenario=failure`
                // const template = await getJsonResult(url)
                // this.failureText = this.replaceText(template.text)
                this.failureText = this.replaceText('您所查询的产品已被多次查询!')
            },
            immediate: true
        }
    },
    methods: {
        replaceText(text) {
            text = text.replace(/\{count\}/g, 1)
            text = text.replace(/\{product\}/g, this.productName)
            text = text.replace(/\{firstQueryTime\}/g, '2020-01-01')
            text = text.replace(/\{lastQueryTime\}/g, '2020-01-01')
            text = text.replace(/\{company\}/g, this.tenant.name)
            text = text.replace(/\{code\}/g, '<测试idis 码>')
            text = text.replace(/\{validateCode\}/g, '123456')
            return text
        }
    }
}
</script>

